*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: rgb(224,252,255);
}
.container{
  padding: 50px 0;
}
.title{
  color: rgb(16,42,66) ;
  font-family: 'Montserrat', sans-serif !important;
}
.flex{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flex p{
  margin: 0;
}
.pointer{
  cursor: pointer;
  color: rgb(16,42,66) !important;
}
.input{
  outline: none;
}
.submit{
  background-color: rgb(44,174,186)  !important;
  color: white !important;
  letter-spacing: 1px;
}
.task{
  color: rgb(16,42,66) !important;
  text-transform: capitalize;
}
